import React from 'react';
import '../css/pay.css';

function BmPay() {
  return (
    <section className="bmpay">
      <article className='pay-left'>
        <img 
          src={`${process.env.PUBLIC_URL}/images/section/payment.png`} 
          alt=''
          className='pay-img'
        />
        <p className='bm-han-pro'></p>
      </article>

      <article className='pay-right bm-han-pro'>
        <div className='pay-text'>
          <h1 className='pay-title'>
               <br/>
            <span className='pay-font-big'></span>
          </h1>
          <p className='pay-subtitle'>
               0.5% 
          </p>
        </div>
        
        <img 
          src={`${process.env.PUBLIC_URL}/images/download/app_qr.png`}
          alt=''
          className='qr'
        />

        <p className='qr-text'>   !</p>
      </article>
    </section>
  );
}

export default BmPay;
