import React from 'react';
import '../css/gift.css';

function BmGift() {
  return (
    <section className="bmgift">
      <article className='gift-left'>
        <img 
          src={`${process.env.PUBLIC_URL}/images/section/foryou.png`} 
          alt=''
          className='gift-img'
        />
        <p className='bm-han-pro'></p>
      </article>

      <article className='gift-right'>
        <h1 className="gift-title bm-han-pro">
          <span className="highlight"></span> <span className="highlight"></span>
        </h1>
        <p className="gift-text bm-han-pro"> ,  ,   </p>

        <div className="gift-grid">
          {[
            { src: 'Rectangle 1.png', label: '' },
            { src: 'Rectangle 2.png', label: '' },
            { src: 'Rectangle 3.png', label: '' },
            { src: 'Rectangle 4.png', label: '' },
            { src: 'Rectangle 5.png', label: '' },
            { src: 'Rectangle 6.png', label: '' },
          ].map((item, idx) => (
            <div className="gift-item" key={idx}>
              <img 
                src={`${process.env.PUBLIC_URL}/images/gift-category/${item.src}`} 
                alt={item.label} 
              />
              <p className="bm-han-pro">{item.label}</p>
            </div>
          ))}
        </div>
      </article>
    </section>
  );
}

export default BmGift;
